html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #e3f2fd;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h2 {
  font-size: 3.5rem;
  border-bottom: 10px #072ac8;
}

.header {
  background-color: #072ac8;
  padding: 0.5rem 2rem;
  min-height: 60rem;
  background-image: url(../img/cielo.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__logo {
  display: flex;
  align-items: center;
  gap: 2rem;
  justify-content: center;
}
.header__nombre {
  color: #FFFFFF;
  font-size: 3rem;
  margin: 0;
}
@media (min-width: 430px) {
  .header__nombre {
    font-size: 5rem;
  }
}
@media (min-width: 768px) {
  .header__nombre {
    font-size: 8.5rem;
  }
}
@media (min-width: 768px) {
  .header__contenido {
    display: flex;
    flex-direction: column;
    height: 58rem;
    justify-content: space-between;
  }
}
.header__slogan {
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 10rem;
}
.header__slogan-titulo, .header__slogan-descripcion {
  margin: 0;
}
.header__slogan-titulo {
  font-size: 4rem;
}
@media (min-width: 768px) {
  .header__slogan-titulo {
    font-size: 5rem;
  }
}
.header__slogan-descripcion {
  font-size: 2.5rem;
  color: #FFFFFF;
}

.navegacion {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  padding: 3rem 0;
}
.navegacion__enlace {
  background-color: transparent;
  color: #FFFFFF;
  padding: 1.5rem 1rem;
  text-align: center;
  border-radius: 10px;
  border: 2px solid #FFFFFF;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    margin-right: 3rem;
    border-radius: 10px;
    transition-property: transform;
    transition-duration: 0.3s;
  }
  .navegacion__enlace__last-of-type {
    margin-right: 0;
  }
  .navegacion__enlace:hover {
    background-color: #1e96fc;
    color: #FFFFFF;
    border: 2px solid #1e96fc;
    transform: scale(0.9);
  }
}

.iconos {
  padding: 8rem 0 0 0;
}
.iconos__titulo {
  text-align: center;
  color: #1e96fc;
  padding-bottom: 8rem;
}
.iconos__grid {
  display: flex;
  justify-content: space-around;
}

.icono {
  padding: 1.5rem;
  background-color: #1e96fc;
  text-align: center;
  border-radius: 10%;
  transition-property: transform;
  transition-duration: 0.5s;
}
@media (min-width: 768px) {
  .icono {
    padding: 4rem;
    border-radius: 25%;
  }
}
.icono:hover {
  transform: rotate(360deg);
}
.icono-invisible {
  display: none;
}
@media (min-width: 768px) {
  .icono-invisible {
    display: inherit;
  }
}

.ciudades {
  background-color: #1e96fc;
  padding-bottom: 5rem;
}
.ciudades__titulo {
  text-align: center;
  margin-top: -3rem;
  padding: 4rem 0;
  color: #FFFFFF;
}

.contenedor-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 1rem;
}

.card {
  position: relative;
}
.card__titulo {
  font-size: 2.5rem;
  color: #FFFFFF;
  margin: 0;
  font-size: "Oswald", sans-serif;
  border-bottom: 2px solid #1e96fc;
  border-top: 2px solid #1e96fc;
  font-weight: bold;
}
.card__info {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
}
.card img {
  height: 20rem;
  width: 40rem;
  margin: 0 auto;
}

.formulario {
  padding: 4rem 0;
}
.formulario__titulo {
  text-align: center;
  color: #1e96fc;
}
.formulario__contenedor {
  border: 3px solid #1e96fc;
  padding: 3rem;
  border-radius: 10px;
}
.formulario__descripcion {
  font-size: 2.3rem;
  text-align: center;
  color: #1e96fc;
}
.formulario__ciudad {
  display: block;
  width: 100%;
  padding: 1rem;
  border: 1px solid #1e96fc;
  background-color: #e3f2fd;
}
.formulario__ciudad:focus {
  outline: none;
}
.formulario__pais {
  display: block;
  width: 100%;
  padding: 1rem;
  border: 1px solid #1e96fc;
  background-color: #e3f2fd;
}
.formulario__pais:focus {
  outline: none;
}
.formulario__boton {
  display: block;
  margin-bottom: 1rem;
  margin-top: 2rem;
  padding: 1.5rem;
  border: none;
  background-color: #1e96fc;
  color: #FFFFFF;
  border-radius: 1rem;
  border: 1px solid #1e96fc;
  transition-property: background-color;
  transition-duration: 300ms;
  margin: 0 auto;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario__boton {
    width: auto;
  }
}
.formulario__boton:hover {
  background-color: #e3f2fd;
  color: #1e96fc;
  cursor: pointer;
}
.formulario form {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .formulario form {
    padding: 0 12rem;
  }
}
.formulario__campo {
  padding: 2rem 0;
}
.formulario__submit {
  padding-bottom: 5rem;
  padding-top: 2rem;
}

.alerta {
  background-color: #1e96fc;
  border: 3px solid #1e96fc;
  color: #FFFFFF;
  padding: 1rem 2rem;
  border-radius: 2rem;
  width: 100%;
  text-align: center;
  margin-bottom: 5rem;
}
.alerta__mensaje {
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: bold;
}

.clima {
  font-size: 8rem;
  margin: 0;
  font-weight: bold;
  border-top: 3px solid #FFFFFF;
  border-bottom: 3px solid #FFFFFF;
  margin-bottom: 2rem;
}

.clima-min, .clima-max {
  margin: 0;
  font-size: 2.2rem;
  color: #e3f2fd;
}
@media (min-width: 768px) {
  .clima-min, .clima-max {
    font-size: 3rem;
  }
}

.clima-ciudad {
  margin: 0;
  font-size: 5rem;
  color: #e3f2fd;
  font-weight: bold;
  line-height: 1;
  padding: 2rem 0;
}

.divclima {
  background-color: #1e96fc;
  border: 3px solid #1e96fc;
  padding: 2rem 2rem;
  border-radius: 2rem;
  width: 100%;
  text-align: center;
  margin-bottom: 3rem;
}

.divMinMax {
  display: flex;
  justify-content: space-around;
}

.clima-calido {
  color: #fcf300;
}

.clima-templado {
  color: #e3f2fd;
}

.clima-frio {
  color: #072ac8;
}

.spinner {
  margin: 100px auto;
  width: 40px;
  height: 40px;
  position: relative;
}

.cube1, .cube2 {
  background-color: #333;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-cubemove 1.8s infinite ease-in-out;
  animation: sk-cubemove 1.8s infinite ease-in-out;
}

.cube2 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}

@-webkit-keyframes sk-cubemove {
  25% {
    -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5);
  }
  50% {
    -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg);
  }
  75% {
    -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
  }
  100% {
    -webkit-transform: rotate(-360deg);
  }
}
@keyframes sk-cubemove {
  25% {
    transform: translateX(42px) rotate(-90deg) scale(0.5);
    -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5);
  }
  50% {
    transform: translateX(42px) translateY(42px) rotate(-179deg);
    -webkit-transform: translateX(42px) translateY(42px) rotate(-179deg);
  }
  50.1% {
    transform: translateX(42px) translateY(42px) rotate(-180deg);
    -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg);
  }
  75% {
    transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
    -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
  }
  100% {
    transform: rotate(-360deg);
    -webkit-transform: rotate(-360deg);
  }
}
.footer {
  background-color: #1e96fc;
  padding: 1.5rem;
  margin-top: 6rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 6rem;
  }
}
.footer__titulo {
  text-align: center;
  color: #FFFFFF;
}
.footer__seccion {
  margin-bottom: 3rem;
}
.footer__descripcion {
  font-size: 2rem;
  color: #FFFFFF;
  text-align: center;
}
.footer__lista {
  line-height: 2;
  font-size: 2rem;
  text-align: center;
  color: #FFFFFF;
}
.footer__copyright {
  background-color: #e3f2fd;
  padding: 1.5rem;
  color: #1e96fc;
  font-weight: bold;
  font-size: 2.2rem;
  text-align: center;
}